.cardContainer{
    width:300px;
    background-color: #fff;
    flex-wrap: wrap;
    box-sizing: border-box;

}

.imgContainer{
    display: flex;
    justify-content: center;
    padding-top: 10px;
    height: 200px;
    background-color: #d37c7c;
    align-items: center;


}
.link{
    background-color: #abffb9;
    width: 150px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
}

.headerContainer{
    padding-left: 10px;
    display: flex;
    justify-content: start;


}

.header{
    text-align: start;


}
.description{
    padding: 10px;
    background-color: #dadaee;
    text-align: start;

}