
.container {
    padding-top: 30px;
    min-height: 100vh;
    background-color: #f78bff;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;

}

.aboutContainer {

    background-color: #a6e5a1;
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;


    gap: 100px;

}


.aboutMe {
    background-color: #dca3a3;
    width: 300px;
    height: 200px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.img {
    width: 300px;
    height: 400px;
    background-color: #5b5553;
}