.container{
    background-color: #d9cd7f;
    min-height: 100%;
   padding-bottom: 100px;
}
.contactContainer{
    background-color: #6be564;
    width: 80%;
    margin: 0 auto;


}
.headerContainer{
    height: 80px;
    background-color: #ff58c5;


}
.sendContainer{
    background-color: #f78bff;
    width:50%;
    height: 300px;
    margin: 0 auto;
    display: flex;
    flex-direction:column;
    justify-content: center;
    gap: 20px;


}
.input{

    height: 40px;
}
.textarea{
    min-height: 120px;


}
@media (max-width:600px){
    .sendContainer{
        width: 100%;
    }
}
