.headerContainer {
    background-color: #cea9a9;
    height: 60px;

}

.nav {
    height: 100%;

    display: flex;
    justify-content: end;
    align-items: center;
    gap: 20px;
    background-color: #ea6a6a;
    width: 80%;
    margin: 0 auto;
}

.navLink {
    background-color: #ffec1c;
    margin: 0;
    text-decoration: none;

}

.menu {
    width:40px;
    height: 40px;
    background-color: #fff;
    display: none;
}

@media (max-width: 600px) {
    .navLink {
        display: none;
    }
    .menu{
        display: flex;
    }
}

